
import { useState } from "react";
import React from "react";

const LoanCalc = () => {
    const [PV, setF] = useState();
    const [n, setN] = useState();
    const [r, setC] = useState();
    const [mvalue, setResult] = useState();

    const calculate = () => {
        // A=p(1+(r/n))^(nt)
        // I= C*F
        const result = (PV * Math.pow((r * (1+r),(n))/((1 + r), (n) - 1)));

        //P x r x ( 1 + r )n / ( ( 1 + r )n - 1 )

        setResult(result.toFixed(2));
    };

    return (
        <div className="row">
            <div className="col-lg-8">
                <form>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Loan Amount in ₹</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setF(e.target.value)} type="text" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className="col-sm-4 calc-label">Annual Interest in %</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setC(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Years</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setN(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <button onClick={() => { calculate(); }} type="button" className="btn btn-primary">Calculate</button>
                </form>
            </div>
            <div className="col-lg-4">
                <div className="pricipalDiv">
                    <h3>Details</h3>
                    <h4>Maturity Value: {mvalue}</h4>
                    <h4>Rate of Interest: %</h4>
                    <h4>YTM: </h4>

                </div>
            </div>
        </div>

    )
}

export default LoanCalc
